import * as GWU from 'gw-utils';
import { Handler, installHandler } from '../handler';
//////////////////////////////////////////////
// EMIT
export class EmitEffect extends Handler {
    constructor() {
        super();
    }
    make(src, dest) {
        if (!src.emit)
            return true;
        if (typeof src.emit !== 'string') {
            throw new Error('emit effects must be string name to emit: { emit: "EVENT" }');
        }
        dest.emit = src.emit;
        return true;
    }
    async fire(config, _map, x, y, ctx) {
        if (config.emit) {
            await GWU.events.emit(config.emit, x, y, ctx);
            return true;
        }
        return false;
    }
}
installHandler('emit', new EmitEffect());
