export function clearCells(map, spawnMap, flags = 0) {
    let didSomething = false;
    const clearAll = (flags & Flags.Effect.E_CLEAR_CELL) === Flags.Effect.E_CLEAR_CELL;
    spawnMap.forEach((v, i, j) => {
        if (!v)
            return;
        const cell = map.cell(i, j);
        if (clearAll) {
            cell.clear();
        }
        else {
            if (flags & Flags.Effect.E_CLEAR_GAS) {
                cell.clearDepth(Flags.Depth.GAS);
            }
            if (flags & Flags.Effect.E_CLEAR_LIQUID) {
                cell.clearDepth(Flags.Depth.LIQUID);
            }
            if (flags & Flags.Effect.E_CLEAR_SURFACE) {
                cell.clearDepth(Flags.Depth.SURFACE);
            }
            if (flags & Flags.Effect.E_CLEAR_GROUND) {
                cell.clearDepth(Flags.Depth.GROUND);
            }
        }
        didSomething = true;
    });
    return didSomething;
}
export function evacuateCreatures(map, blockingMap) {
    let didSomething = false;
    map.eachActor((a) => {
        if (!blockingMap[a.x][a.y])
            return;
        const loc = map.rng.matchingLocNear(a.x, a.y, (x, y) => {
            if (!map.hasXY(x, y))
                return false;
            if (blockingMap[x][y])
                return false;
            const c = map.cell(x, y);
            return !a.forbidsCell(c);
        });
        if (loc && loc[0] >= 0 && loc[1] >= 0) {
            map.removeActor(a);
            map.addActor(loc[0], loc[1], a);
            // map.redrawXY(loc[0], loc[1]);
            didSomething = true;
        }
    });
    return didSomething;
}
export function evacuateItems(map, blockingMap) {
    let didSomething = false;
    map.eachItem((i) => {
        if (!blockingMap[i.x][i.y])
            return;
        const loc = map.rng.matchingLocNear(i.x, i.y, (x, y) => {
            if (!map.hasXY(x, y))
                return false;
            if (blockingMap[x][y])
                return false;
            const dest = map.cell(x, y);
            return !i.forbidsCell(dest);
        });
        if (loc && loc[0] >= 0 && loc[1] >= 0) {
            map.removeItem(i);
            map.addItem(loc[0], loc[1], i);
            // map.redrawXY(loc[0], loc[1]);
            didSomething = true;
        }
    });
    return didSomething;
}
class ClearTileEffect extends Handler {
    constructor() {
        super();
    }
    make(src, dest) {
        if (!src.clear)
            return true;
        let config = src.clear;
        let layers = 0;
        if (typeof config === 'string') {
            config = config.split(/[,|]/).map((t) => t.trim());
        }
        if (config === true) {
            layers = Flags.Depth.ALL_LAYERS;
        }
        else if (typeof config === 'number') {
            layers = config;
        }
        else if (Array.isArray(config)) {
            layers = config.reduce((out, v) => {
                if (typeof v === 'number')
                    return out | v;
                const depth = Flags.Depth[v] || 0;
                return out | depth;
            }, 0);
        }
        else {
            throw new Error('clear effect must have number or string config.');
        }
        dest.clear = layers;
        return layers > 0;
    }
    async fire(config, map, x, y, ctx) {
        return this.fireSync(config, map, x, y, ctx);
    }
    fireSync(config, map, x, y, _ctx) {
        if (!config.clear)
            return false;
        if (!map)
            return false;
        const cell = map.cell(x, y);
        return cell.clearDepth(config.clear);
    }
}
installHandler('clear', new ClearTileEffect());
